.catalog__title {
  @include oswaldReg;
  @include fontStyle(36px,60px);
  margin: 14px 20px 0;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    margin: 64px 30px 0;
  }

  @media (min-width: $desktop-width) {
    margin: 68px 40px 0;
  }
}

.catalog__list {
  @include ulStyle;
  display: flex;
  flex-direction: column;
  margin-top: 27px;
  padding-bottom: 2px;


  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 22px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    margin-top: 27px;
  }
}

.catalog__item {
  border-top: 1px solid $grey;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    margin-top: 142px;
    border: none;
    width: 50%;
    box-sizing: border-box;
    display: flex;
    padding: 0 30px;
  }

  &:last-child {
    border-bottom: 1px solid $grey;

    @media (min-width: $tablet-width) {
      border: none;
    }
  }

  @media (min-width: $desktop-width) {
    max-width: 25%;
    box-sizing: border-box;
    padding: 0 40px 2px;
    margin-top: 137px;
  }
}

.catalog__item-wrapper {
  width: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 80px;
  margin: 0 auto;

  &--more {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 27px;
      margin: 0 auto;
      width: 30px;
      height: 30px;
      opacity: 0.2;
      background-image: url("../img/icon-plus.svg");
      background-repeat: no-repeat;
      background-position: center;
    }

    @media (min-width: $tablet-width) {
      &::before {
        width: 60px;
        height: 60px;
        top: 50px;
      }
    }

    @media (min-width: $desktop-width) {
      &::before {
        top: 53px;
      }
    }
  }

  @media (min-width: $tablet-width) {
    box-sizing: border-box;
    padding: 163px 37px 38px 38px;
    width: 100%;
    min-height: 430px;
    text-align: center;
    border: 1px solid #e6e6e6;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    padding: 161px 36px 38px 37px;

    &--more {
      padding: 163px 36px 42px 37px;
    }
  }
}

.catalog__more-title {
  @include oswaldReg;
  @include fontStyle(16px,20px);
  text-transform: uppercase;
  color: #111111;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    padding: 0 35px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
    width: 100%;
  }
}

.catalog__more-text {
  @include fontStyle(12px,16px);
  color: #444444;
  font-weight: 400px;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 18px;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    padding: 26px 30px 0 30px;
  }

  @media (min-width: $desktop-width) {
    padding: 28px 0 55px;
    width: 100%;
  }
}
