.gift {
  margin: 40px auto 34px auto;
  min-height: 289px;
  max-width: 280px;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-color: #68b738;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  padding: 65px 48px 44px 50px;
  text-align: center;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    margin: 70px 0 0 0;
    padding: 65px 110px 65px 91px;
    box-sizing: border-box;
    justify-content: space-between;
    flex-direction: row;
    min-height: 200px;
    max-width: 100%;
    text-align: start;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    padding: 63px 35px 40px;
    margin: 0 0 0 80px;
    min-height: 290;
    flex-direction: column;
    text-align: center;
    align-items: center;
    background-image: url("../img/gift-photo-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }
  }
}

.gift__desc {
  font-size: 16px;
  line-height: 20px;
  color: $white;
  margin: 0;

  @media (min-width: $tablet-width) {
    padding: 0;
    width: 175px;
  }
}

.gift__icon {
  fill: $white;

  @media (min-width: $tablet-width) {
    padding-bottom: 10px;
  }
}
